.btn {
  position: relative;
  display: block;
  height: 49px;
  font-size: 16px;
  line-height: 49px;
  font-weight: 700;
  border-radius: 35px;
  outline: none;
  font-family: "Montserrat", serif;
  font-size: 16px;
  color: #fff;
  background: transparent;
  border: none;
  z-index: 3;
  text-align: center;
  cursor: pointer;
}

.btn-wrap {
  position: relative;
  padding: 0 30px 0 30px;
  max-width: 220px;
  width: 100%;
  border: none;
  border-radius: 35px;
  color: #fff;
  background-image: linear-gradient(180deg, #ffbe34 0%, #ff8e5e 100%);
  box-shadow: inset 0px 1px 0px rgba(239, 7, 7, 0.5);
  transition: 0.25s ease-in-out;
  z-index: 1;
  cursor: pointer;
}

.btn-wrap:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 35px;
  background-image: linear-gradient(180deg, #ff8e5e 0%, #ffbe34 100%);
  transition: opacity 0.25s ease-in-out;
  z-index: 2;
  opacity: 0;
}

.btn-transparent {
  border: 1px solid #fff;
  box-shadow: inset 0px 0px 0px 1px transparent;
  background: transparent;
  transition: color 0.25s ease-in-out, border-color 0.25s ease-in-out, box-shadow 0.25s ease-in-out, color 0.25s ease-in-out;
}

.input {
  height: 49px;
  background: #eef2fa;
  font-size: 16px;
  font-weight: 400;
  line-height: 43px;
  border-radius: 10px;
  outline: none;
  box-shadow: transparent;
  transition: box-shadow;
}

textarea {
  resize: none;
}

.burger {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  background: transparent;
  border: none;
  outline: none;
  z-index: 1000;
}

.burger:hover {
  cursor: pointer;
}

.burger:hover i:before {
  margin-top: -14px;
}

.burger:hover i:after {
  margin-top: 14px;
}

.burger i {
  display: block;
  width: 40px;
  height: 2px;
  background: #ffd943;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}

.burger i:before,
.burger i:after {
  content: "";
  position: absolute;
  height: 2px;
  background: #ffd943;
  width: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}

.burger i:before {
  margin-top: -12px;
}

.burger i:after {
  margin-top: 12px;
}

.burger.active {
  background: transparent;
}

.burger.active i {
  background: transparent;
}

.burger.active i:before,
.burger.active i:after {
  left: -5px;
  width: 50px;
}

.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}

.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}

.burger.active:hover i:after,
.burger.active:hover i:before {
  width: 60px;
  margin: 0;
  left: -10px;
  top: 0;
}

