@keyframes dash {
  from {
    opacity: 0;
  }

  to {
    stroke-dashoffset: 2;
    stroke-dasharray: 10;
    opacity: 1;
  }
}

@keyframes plane {
  0% {
    opacity: 0;
    transform: translate(100px, 50px);
  }

  50% {
    transform: translate(80px, 40px);
  }

  75% {
    transform: translate(70px, 20px);
  }

  100% {
    transform: translate(0, 0);
  }
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 50px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInUp {
  animation-name: fadeInUp;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.fadeIn {
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: both;
}

